@import url("https://use.typekit.net/wjp0tiv.css");
@import url("https://use.typekit.net/veh1bye.css");
.default-background {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.default-center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  color: #202020;
  font-family: "roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  -webkit-appearance: none;
  line-height: 1.1;
}

body,
html {
  height: 100%;
}

body,
main {
  overflow-x: hidden;
}

p {
  width: 100%;
}
p:not(:last-of-type) {
  margin-bottom: 20px;
}

h1, h2, h3, h4, h5, h6 {
  line-height: 1.25;
  font-family: "ivypresto-display", serif;
}

@media (min-width: 767px) {
  h1 {
    font-family: ivypresto-display, serif;
    font-size: clamp(2.3688rem, 2.051rem + 1.2281vw, 3.1563rem);
    font-weight: 900;
    line-height: 100%;
    text-transform: uppercase;
  }
}

h2 {
  font-size: 24px;
  font-style: normal;
  font-weight: 300;
  line-height: 115%;
}
@media (min-width: 1024px) {
  h2 {
    font-size: 48px;
  }
}

h3 {
  font-size: 20px;
  font-style: normal;
  font-weight: 300;
  line-height: 115%;
}
@media (min-width: 1024px) {
  h3 {
    font-size: 40px;
  }
}

h4 {
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 115%;
}
@media (min-width: 1024px) {
  h4 {
    font-size: 32px;
  }
}

h5 {
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
}

h6 {
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
}

.title-large {
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
}

.title-medium {
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
}

.title-small {
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
}

.body-large {
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 145%;
}

a {
  transition: all 0.3s ease-in-out;
  text-decoration: none;
}

span,
u,
b,
strong,
i,
em,
blockquote,
a {
  color: inherit;
  font-size: inherit;
  font-family: inherit;
  font-weight: inherit;
}

i,
em,
blockquote {
  font-style: italic;
}

b,
strong {
  font-weight: 600;
}

img {
  object-fit: cover;
  height: auto;
}

blockquote {
  padding-left: 20px;
  border-left: solid 1px #202020;
}

svg {
  transition: all 0.3s ease-in-out;
}
svg * {
  transition: all 0.3s ease-in-out;
}

.button {
  font-weight: 700;
  padding: 10px 20px;
  border-radius: 5px;
  border: solid 2px;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.3);
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.button:hover {
  background-color: #ff8a60;
  border-color: #ff8a60;
  color: #f4f4f4;
}

/* jQuery animation; slide up assets on scroll
* Add the class 'module' followed by either 'fadeUp', 'fadeLeft', 'fadeRight'
* to elements that you wish to be animated on scroll. 
*/
.come-in {
  opacity: 0;
}
.come-in.fadeUp {
  transform: translateY(150px);
  opacity: 0;
  animation: fadeUp 1.5s ease forwards;
}
.come-in.fadeLeft {
  opacity: 0;
  animation: fadeUp 1.5s ease forwards;
  transform: translateY(150px);
}
@media (min-width: 1024px) {
  .come-in.fadeLeft {
    transform: translateX(-150px);
    animation: fadeLeft 1.5s ease forwards;
  }
}
.come-in.fadeRight {
  opacity: 0;
  animation: fadeUp 1.5s ease forwards;
  transform: translateY(150px);
}
@media (min-width: 1024px) {
  .come-in.fadeRight {
    transform: translateX(150px);
    animation: fadeRight 1.5s ease forwards;
  }
}

.already-visible {
  transform: translate(0, 0) !important;
  opacity: 1 !important;
  animation: none !important;
}

@keyframes fadeUp {
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes fadeLeft {
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes fadeRight {
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
.on-load {
  transform: translateY(0) !important;
  opacity: 1 !important;
}

.post-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 20px;
}
@media (min-width: 767px) {
  .post-container {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: stretch;
  }
}
@media (min-width: 1024px) {
  .post-container {
    margin-bottom: 50px;
  }
}
.post-container .post {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}
@media (min-width: 767px) {
  .post-container .post {
    width: calc(50% - 10px);
  }
}
@media (min-width: 1024px) {
  .post-container .post {
    width: calc(33.33% - 10px);
  }
}
.post-container .post img {
  width: 100%;
  object-fit: cover;
  height: 250px;
}
.post-container .post .title-button {
  padding: 20px;
  background-color: #f4f4f4;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-grow: 1;
}
.post-container .post .title-button p {
  width: auto;
  white-space: nowrap;
}
.post-container .post .title-button h3 {
  transition: all 0.3s ease-in-out;
  width: auto;
  margin-right: 20px;
}
.post-container .post:hover .title-button h3 {
  color: #ff8a60;
}

.blog-filter {
  flex-direction: row !important;
  justify-content: space-between !important;
  align-items: center;
  position: relative;
}
.blog-filter:after {
  width: 15px;
  height: 15px;
  top: calc(50% - 7.5px);
  right: 20px;
  content: "";
  position: absolute;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url("/wp-content/themes/pixelbase/images/arrow.svg");
  pointer-events: none;
  transition: all 0.3s ease-in-out;
}
.blog-filter .search, .blog-filter .category-filter-dropdown {
  padding: 10px 20px;
  outline: none;
  border: solid 2px #202020;
  color: #202020;
  font-weight: 700;
}
.blog-filter .search::placeholder, .blog-filter .category-filter-dropdown::placeholder {
  color: #202020;
}
.blog-filter .search {
  border: none;
  border-bottom: solid 2px #202020;
  padding: 10px 0px;
}
@media (min-width: 1024px) {
  .blog-filter .search {
    margin-left: 50%;
    transform: translateX(-50%);
  }
}
.blog-filter .category-filter-dropdown {
  cursor: pointer;
  padding-right: 60px;
}

.no-results {
  display: none !important;
}
.no-results.visible {
  display: flex !important;
}
.no-results h3 {
  text-align: center;
  width: 100%;
}
.no-results h3 span {
  color: #ff8a60;
}

.blog-author-next {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

footer {
  width: 100vw;
  max-width: 100%;
  padding: 10px 0px;
  background-color: #202020;
}
footer * {
  color: #f4f4f4;
}
footer .signature {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 12px;
  justify-content: flex-end;
  width: 100%;
}
footer .signature svg {
  margin-left: 7px;
  width: 80px;
  height: auto;
}
footer .signature svg * {
  fill: #f4f4f4 !important;
}
footer .signature svg:hover * {
  fill: #ff8a60 !important;
}

header {
  width: 100vw;
  max-width: 100%;
  margin-bottom: 20px;
}
@media (min-width: 1024px) {
  header {
    margin-bottom: 50px;
  }
}
header nav a {
  text-decoration: underline;
}
header nav a:hover {
  color: #ff8a60;
}

.hero-section.home-hero {
  background-size: contain;
  background-position: center right;
  background-repeat: repeat;
}
.hero-section .container {
  display: flex;
}