// Fonts.
@import url("https://use.typekit.net/wjp0tiv.css");
@import url("https://use.typekit.net/veh1bye.css");
$font: "roboto", sans-serif;
$heading-font: "ivypresto-display", serif;


// Media querys.
$mobile: "(min-width: 767px)";
$tablet: "(min-width: 1024px)";
$desktop: "(min-width: 1200px)";

// Misc.
$max-width: 1600px;
$shadow: 0 5px 10px 0 rgba(0,0,0,0.3);
$ease: all 0.3s ease-in-out;

// Default classes, add these to elements to prevent repeating yourself.
.default-background {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.default-center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

// SCSS includes.
@mixin default-psuedo {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  transition: $ease;
}

// Font mixin with breakpoint checks
@mixin font-style($id) {
  @each $breakpoint, $properties in map-get($font-styles, $id) {
      // Check for 'small' breakpoint
      @if $breakpoint == small {
          @media #{$mobile} {
              @each $property, $value in $properties {
                  #{$property}: #{$value};
              }
          }
      }
      // Check for 'medium' breakpoint
      @else if $breakpoint == medium {
          @media #{$tablet} {
              @each $property, $value in $properties {
                  #{$property}: #{$value};
              }
          }
      }
      // Check for 'large' breakpoint
      @else if $breakpoint == large {
          @media #{$desktop} {
              @each $property, $value in $properties {
                  #{$property}: #{$value};
              }
          }
      }
  }
}
