// All footer styles.

footer {
  width: 100vw;
  max-width: 100%;
  padding: 10px 0px;
  background-color: $black;
    * {
      color: $white-base;
    }

    .signature {
      display: flex;
      flex-direction: row;
      align-items: center;
      font-size: 12px;
      justify-content: flex-end;
      width: 100%;
        svg {
          margin-left: 7px;
          width: 80px;
          height: auto;
            * {
              fill:$white-base !important;
            }
            &:hover {
              * {
                fill: $primary-base !important;
              }
            }
        }
    }
}