.post-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 20px;
    @media #{$mobile} {
        flex-direction: row;
        flex-wrap: wrap;
        align-items: stretch;
    }
    @media #{$tablet} {
        margin-bottom: 50px;
    }
        .post {
            width: 100%;
            display: flex;
            flex-direction: column;
            margin-bottom: 15px;
            @media #{$mobile} {
                width: calc(50% - 10px);
            }
            @media #{$tablet} {
                width: calc(33.33% - 10px);
            }
                img {
                    width: 100%;
                    object-fit: cover;
                    height: 250px;
                }
                .title-button {
                    padding: 20px;
                    background-color: $white-base;
                    box-shadow: $shadow;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    flex-grow: 1;
                        p {
                            width: auto;
                            white-space: nowrap;
                        }
                        h3 {
                            transition: $ease;
                            width: auto;
                            margin-right: 20px;
                        }
                }
                &:hover {
                    .title-button {
                        h3 {
                            color: $primary-base;
                        }
                    }
                }
        }
}

.blog-filter {
    flex-direction: row !important;
    justify-content: space-between !important;
    align-items: center;
    position: relative;
        &:after {
            width: 15px;
            height: 15px;
            top: calc(50% - 7.5px);
            right: 20px;
            content: "";
            position: absolute;
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
            background-image: url('/wp-content/themes/pixelbase/images/arrow.svg');
            pointer-events: none;
            transition: $ease;
        }
        .search, .category-filter-dropdown {
            padding: 10px 20px;
            outline: none;
            border: solid 2px $black;
            color: $black;
            font-weight: 700;
                &::placeholder {
                    color: $black;
                }
        }
        .search {
            border: none;
            border-bottom: solid 2px $black;
            padding: 10px 0px;
            @media #{$tablet} {
                margin-left: 50%;
                transform: translateX(-50%);
            }
        }
        .category-filter-dropdown {
            cursor: pointer;
            padding-right: 60px;
        }
}

.no-results {
    display: none !important;
        &.visible {
            display: flex !important;
        }
        h3 {
            text-align: center;
            width: 100%;
                span {
                    color: $primary-base;
                }
        }
}

.blog-author-next {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}