// Default element styles and main containers.
* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  color: $black;
  font-family: $font;
  font-weight: 400;
  font-size: 16px;
  -webkit-appearance: none;
  line-height: 1.1;
}

body,
html {
  height: 100%;
}

body,
main {
	overflow-x: hidden;
}

p {
  width: 100%;

  &:not(:last-of-type) {
    margin-bottom: 20px;
  }
}

h1,h2, h3, h4, h5, h6 {
  line-height: 1.25;
  font-family: $heading-font;
}

h1 {
  @include font-style(heading-1);
}

h2 {
  font-size: 24px;
  font-style: normal;
  font-weight: 300;
  line-height: 115%;

  @media #{$tablet} {
    font-size: 48px;
  }
}

h3 {
  font-size: 20px;
  font-style: normal;
  font-weight: 300;
  line-height: 115%;

  @media #{$tablet} {
    font-size: 40px;
  }
}

h4 {
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 115%;

  @media #{$tablet} {
    font-size: 32px;
  }
}

h5 {
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
}

h6 {
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
}

.title-large {
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
}

.title-medium {
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
}

.title-small {
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
}

.body-large {
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 145%;
}

.body-medium {

}

a {
  transition: $ease;
  text-decoration: none;
}

span,
u,
b,
strong,
i,
em,
blockquote,
a {
  color: inherit;
  font-size: inherit;
  font-family: inherit;
  font-weight: inherit;
}

i,
em,
blockquote {
  font-style: italic;
}

b,
strong {
  font-weight: 600;
}

img {
  object-fit: cover;
  height: auto;
}

blockquote {
  padding-left: 20px;
  border-left: solid 1px $black;
}

svg {
  transition: $ease;
    * {
      transition: $ease;
    }
}