.button {
    font-weight: 700;
    padding: 10px 20px;
    border-radius: 5px;
    border: solid 2px;
    box-shadow: $shadow;
    cursor: pointer;
    transition: $ease;
        &:hover {
            background-color: $primary-base;
            border-color: $primary-base;
            color: $white-base;
        }
}